 procesos de suministro de agua y saneamiento. El acceso al agua potable y saneamiento básico es un factor determinante para mejorar las condiciones de habitabilidad de las viviendas, impactar en la situación de pobreza y salud de la población, fomentar el desarrollo económico, social, ambiental y tecnológico del país, así como para ofrecer a los aprendices una formación en tecnologías de producción sostenible, en armonía con los propósitos del Gobierno Nacional.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container tarjeta tarjeta__template--azul-claro px-3 py-4 px-sm-5 pb-sm-5 mb-5"},[_c('div',{staticClass:"titulo__template--a mb-4"},[_c('span',{staticClass:"h6"},[_vm._v("3. Competencias a desarrollar")])]),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-md-12"},[_c('div',{staticClass:"tarjeta tarjeta-numerada"},[_c('p',{staticClass:"text-bold"},[_vm._v("Específicas")]),_c('br'),_c('ul',{staticClass:"lista-ul"},[_c('li',[_c('span',{staticClass:"badge bg-outlined"},[_vm._v("280201239")]),_vm._v("Comprobar funcionamiento de sistemas de agua de acuerdo con normas técnicas.")]),_c('li',[_c('span',{staticClass:"badge bg-outlined"},[_vm._v("280201225")]),_vm._v("Supervisar actividades de servicio de aseo de acuerdo con planeación y normativa.")]),_c('li',[_c('span',{staticClass:"badge bg-outlined"},[_vm._v("280201221 ")]),_vm._v("Potabilizar agua de acuerdo con normas técnicas.")]),_c('li',[_c('span',{staticClass:"badge bg-outlined"},[_vm._v("280201226 ")]),_vm._v("Tratar agua residual de acuerdo con los procedimientos técnicos.")]),_c('li',[_c('span',{staticClass:"badge bg-outlined"},[_vm._v("230101239")]),_vm._v("Promocionar acciones de salud de acuerdo con la normativa de salud pública.")]),_c('li',[_c('span',{staticClass:"badge bg-outlined"},[_vm._v("280201214 ")]),_vm._v("Recolectar muestras de agua de acuerdo con procedimientos y normas técnicas.")]),_c('li',[_c('span',{staticClass: